
* {
  position: relative;
  font-family: "Microsoft Yahei", sans-serif;
}
html,
body {
  margin: 0;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #ffc954;
}
#buylist {
  display: inline-block;
  padding: 20px;
  border: solid 4px #444;
  background-color: #f3f3f3;
  -webkit-box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.2);
}
.title {
  margin-top: -20px;
  margin-left: -20px;
  margin-right: -20px;
  margin-bottom: 20px;
  background-color: #ff3d4a;
  color: #f3f3f3;
  padding: 10px 20px;
  border-bottom: solid 4px #444;
}
.title > * {
    display: inline-block;
}
.title h1 {
    margin: 0;
    font-weight: 600;
    width: 70%;
}
.title .cart {
    position: relative;
    font-size: 18px;
    width: 30%;
    text-align: right;
}
.title .cart .fa {
      font-size: 38px;
}
.title .cart span {
      position: absolute;
      text-align: center;
      font-weight: bold;
      right: -14px;
      top: -12px;
      width: 30px;
      height: 30px;
      line-height: 30px;
      border-radius: 100%;
      background-color: #ffc954;
      color: #444;
}
#items_list {
  margin-top: 10px;
  padding: 5px;
}
.control_panel {
  padding: 20px;
}
.buy_item {
  display: block;
  width: 100%;
  padding: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: solid 3px #444;
  font-weight: 900;
}
.buy_item > * {
    display: inline-block;
}
.buy_item .id {
    width: 5%;
    text-align: center;
    background-color: #ffd6d9;
    color: #6a6a6a;
}
.buy_item .name {
    width: 20%;
    font-size: 0.8em;
}
.buy_item input {
    width: 100px;
    border: solid 3px;
    padding: 2px;
    background-color: #e6e6e6;
}
.buy_item .price {
    display: inline-block;
    width: 22%;
    padding: 0 5px;
    background-color: #ffc954;
}
.buy_item .price::before {
      content: " $";
}
.buy_item .quantity {
    width: 33%;
    background-color: #444;
    color: #f3f3f3;
    text-align: center;
}
.buy_item .quantity .minus,
    .buy_item .quantity .num,
    .buy_item .quantity .add {
      display: inline-block;
}
.buy_item .quantity .num {
      width: 37%;
      background-color: transparent;
      color: #fff;
      border: 0;
      outline: 0;
      text-align: center;
      font-size: 0.8em;
}
.buy_item .quantity .minus,
    .buy_item .quantity .add {
      background-color: #676767;
      color: #f3f3f3;
      border: 0;
      outline: 0;
      -webkit-appearance: none;
         -moz-appearance: none;
              appearance: none;
      width: 30%;
      font-size: 22px;
      text-align: center;
      cursor: pointer;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
      -webkit-transition: all 0.3s linear;
      transition: all 0.3s linear;
}
.buy_item .quantity .minus:hover,
      .buy_item .quantity .add:hover {
        background-color: silver;
        color: #444;
}
.buy_item .add_cart {
    color: #ffb921;
    padding: 0px 6px;
    cursor: pointer;
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
}
.buy_item .add_cart:hover {
      background-color: #ffc13b;
      color: white;
}
.buy_item .del_btn {
    color: #ff3d4a;
    display: inline-block;
    position: absolute;
    padding: 0px 6px;
    right: 10px;
    top: 7px;
    cursor: pointer;
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
}
.buy_item .del_btn:hover {
      background-color: #ff3d4a;
      color: white;
}
.buy_item .addbtn {
    background-color: #ffd6d9;
    cursor: pointer;
    margin-left: 10px;
    padding: 5px;
    border: solid 3px #777777;
    -webkit-transition: all 0.3s linear;
    transition: all 0.3s linear;
}
.buy_item .addbtn:hover {
      background-color: #ff3d4a;
      color: white;
}
.total {
  display: block;
  width: 100%;
  background-color: #ff3d4a;
  color: #f3f3f3;
  text-align: right;
}
.total .price {
    background-color: transparent;
}
